* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

li {
    list-style: none;
}

body {
    font-family: "Comfortaa";
    background: $main;

}

.container {
    @include container ();
}

.row {
    @include row()
}

::after,
::before {
    font-family: "fontawesome";
}

.icon {
    &-film::before {
        content: "\f008";
        color: $white;
        font-weight: 300;
    }

    &-play::before {
        position: absolute;
        opacity: 0.4;
        content: "\f04b";
        font-weight: 900;
        color: $white;

    }

    &-play::after {
        content: "\10f04b";
        font-weight: 900;
        color: $white;
        transition: 0.5s;


    }

    &-play:hover::after {
        color: $orange;
    }
   

    &-burger::before {
        content: "\f0c9";
        font-weight: 300;
        
    }

    &-market::before {
        content: "\f3ab";
        font-weight: 300;
    }

    &-apple::before {
        content: "\f179";
        font-weight: 300;
    }
}

.btn {
    font-family: "Roboto";
    font-weight: 500;
    font-size: 14px;
    line-height: 12px;
    color: $black;
    padding: 17px 48px;
    background: $orange;
    border-radius: 4px;
    margin-right: 30px;
    transition: 0.5s;
    display: inline-block;


}

.btn:hover {
    color: $white;
    background: $main;
}

.title {
    font-size: 36px;
    font-weight: 400;
    margin-top: 60px;
    margin-bottom: 48px;
    text-transform: capitalize;
    display: flex;

    &__link {
        color: $orange;

        &::after {
            content: "";
            margin-top: 2px;
            height: 2px;
            background: $orange;
            display: block;
        }
    }


}