.ads {
    background: $ads;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px 0;
    color: $white;

    &__icon {
        line-height: 40px;
        font-size: 54px;
        margin-right: 24px;
    }
    &__info {
        font-size: 16px;
        max-width: 166px;
        line-height: 17,84px;
        font-weight: 400;
    }
}



