.coll {
    padding-top: 60px;
    padding-bottom: 60px;


    &__cinemas {
        position: relative;
        border-radius: 6px;
        overflow: hidden;
        
    }

    &__img {
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &__content {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 8px 10px;
        background: $coll;
 
    }

    &__article {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: $white;
    }
    &__icon{
        line-height: 48px;
        font-size: 50px;
        display: inline-block;
    }
    &__title {
        font-size: 20px;
        font-weight: 700;
        margin-top: 15px;
        text-align: center;
    }

    &__item {
        display: grid;
        grid-template-rows: repeat(5, 1ft);
        gap: 15px;
        flex-direction: column;
    }

    &__value {
        width: 20px;
        height: 25px;
        border-radius: 2px;
        background: $main;
       
    }

}
.cinemas{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    @include media($md) {
        grid-template-columns: repeat(1, 0.85fr);
        justify-content: center;
    }
    
}