.header {
    background: $nav;
    backdrop-filter: blur(15px);
    position: sticky;
    top: 0;
    z-index: 999;
}

.nav {
    @include row(0);
    justify-content: space-between;
    align-items: center;

    &__burger {
        visibility: hidden;
        background: none;
        outline: none;
        border: none;
        color: $white;
        font-size: 35px;
        line-height: 40px;
        padding: 30px 0;

        @include media($sm) {
            visibility: visible;

        }
    }


    a {
        color: $white;
    }

    .logo {
        font-weight: 700;
        font-size: 18px;

        img {
            margin-right: 5px;
        }
    }
}

.menu {
    @include row(0);

    @include media($sm) {
        position: absolute;
        right: 15px;
        top: 100px;
        background: $nav;
        flex-direction: column;
        min-width: 200px;
        border-radius: 0px 0px 5px 5px;
        transform: scaleY(0);
        transition: 0.5s;
        transform-origin: 50% 0 0;
        opacity: 0;

        &.active {
            transform: scaleY(1);
            opacity: 1;

        }

    }

    &__link {
        position: relative;
        font-family: "Montserrat";
        font-size: 14px;
        padding: 43px 6px 40px 6px;
        display: block;
        margin-left: 18px;
        transition: 0.5s;
        border-radius: 2px 2px 0px 0px;

        @include media ($sm) {
            padding: 20px;
            margin-left: 0;
        }

        &::after {
            content: '';
            height: 2px;
            background: $nav_link;
            width: 0;
            display: block;
            position: absolute;
            bottom: 0;
            left: 50%;
            transition: 0.5s;

            @include media($sm) {
                left: 0;
                width: 2px;
                height: 0;
                bottom: 50%;

            }
        }

        &:hover {
            color: $nav_link;

            @include media($sm) {
                background: rgba($nav_link, 0.1);
            }

            &::after {
                width: 100%;
                left: 0;

                @include media($sm) {
                    bottom: 0;
                    width: 2px;
                    height: 100%;

                }
            }
        }
    }

}