.series__section1 {
    @include media($sm) {
        display: grid;
        grid-template-columns: 90%;
        justify-content: center;
    }

}

.series__ads {
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 30px;
}

.section1 {
    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        position: relative;
        background: $ser;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 104px 15px;
        color: $white;
    }

    &__title {
        font-size: 40px;
        font-weight: 700;
        text-align: center;

        @include media ($xs) {
            font-size: 32px;
        }

        @include media (400px) {
            font-size: 27px;
        }
    }

    &__desc {
        font-size: 16px;
        font-weight: 400;
        text-align: center;
        line-height: 24px;
        margin: 30px 0;
        @include limit(4);
    }

    &__link {
        margin: 0;
    }
}

.part1 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);

    @include media ($sm) {
        grid-template-columns: repeat(1, 0.9fr);
        justify-content: center;
    }

    &__seria {
        position: relative;
        overflow: hidden;
        border-radius: 6px;
        display: flex;
    }

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        background: $ser;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 143px;
        padding-bottom: 162px;
        position: relative;
        color: $white;
        flex-grow: 1;

        @include media ($sm) {
            padding-top: 45px;
            padding-bottom: 50px;
        }
    }

    &__link {
        line-height: 48px;
        font-size: 50px;
    }

    &__title {
        margin-top: 20px;
        font-weight: 700;
        font-size: 24px;
        text-align: center;
    }

}

.part2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    @include media ($sm) {
        grid-template-columns: 90%;
        justify-content: center;
    }

    &__seria {
        position: relative;
        overflow: hidden;
        border-radius: 6px;
    }

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        background: $ser;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 45px;
        padding-bottom: 50px;
        position: relative;
        color: $white;
    }

    &__link {
        line-height: 48px;
        font-size: 50px;
    }

    &__title {
        margin-top: 20px;
        font-weight: 700;
        font-size: 24px;
    }

}

.series__section2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    @include media ($sm) {
        display: flex;
        flex-direction: column;
    }
}

.series {
    padding-bottom: 60px;
}