@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Comfortaa";
  src: url(../fonts/Comfortaa/Comfortaa-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Regular.ttf);
  font-weight: 400;
}

@font-face {
  font-family: "Montserrat";
  src: url(../fonts/Montserrat/Montserrat-Bold.ttf);
  font-weight: 700;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-duotone.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Medium.ttf);
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Regular.ttf);
  font-weight: 400;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  font-family: "Comfortaa";
  background: #040D1F;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::after,
::before {
  font-family: "fontawesome";
}

.icon-film::before {
  content: "\f008";
  color: #fff;
  font-weight: 300;
}

.icon-play::before {
  position: absolute;
  opacity: 0.4;
  content: "\f04b";
  font-weight: 900;
  color: #fff;
}

.icon-play::after {
  content: "\10f04b";
  font-weight: 900;
  color: #fff;
  transition: 0.5s;
}

.icon-play:hover::after {
  color: #F18128;
}

.icon-burger::before {
  content: "\f0c9";
  font-weight: 300;
}

.icon-market::before {
  content: "\f3ab";
  font-weight: 300;
}

.icon-apple::before {
  content: "\f179";
  font-weight: 300;
}

.btn {
  font-family: "Roboto";
  font-weight: 500;
  font-size: 14px;
  line-height: 12px;
  color: #000;
  padding: 17px 48px;
  background: #F18128;
  border-radius: 4px;
  margin-right: 30px;
  transition: 0.5s;
  display: inline-block;
}

.btn:hover {
  color: #fff;
  background: #040D1F;
}

.title {
  font-size: 36px;
  font-weight: 400;
  margin-top: 60px;
  margin-bottom: 48px;
  text-transform: capitalize;
  display: flex;
}

.title__link {
  color: #F18128;
}

.title__link::after {
  content: "";
  margin-top: 2px;
  height: 2px;
  background: #F18128;
  display: block;
}

.ads {
  background: linear-gradient(90.09deg, #3800AF 0%, #BF7204 97.52%, #C27500 100%, #C27500 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 20px 0;
  color: #fff;
}

.ads__icon {
  line-height: 40px;
  font-size: 54px;
  margin-right: 24px;
}

.ads__info {
  font-size: 16px;
  max-width: 166px;
  line-height: 17,84px;
  font-weight: 400;
}

.header {
  background: rgba(1, 3, 5, 0.8);
  backdrop-filter: blur(15px);
  position: sticky;
  top: 0;
  z-index: 999;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.nav__burger {
  visibility: hidden;
  background: none;
  outline: none;
  border: none;
  color: #fff;
  font-size: 35px;
  line-height: 40px;
  padding: 30px 0;
}

@media (max-width: 768px) {
  .nav__burger {
    visibility: visible;
  }
}

.nav a {
  color: #fff;
}

.nav .logo {
  font-weight: 700;
  font-size: 18px;
}

.nav .logo img {
  margin-right: 5px;
}

.menu {
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 768px) {
  .menu {
    position: absolute;
    right: 15px;
    top: 100px;
    background: rgba(1, 3, 5, 0.8);
    flex-direction: column;
    min-width: 200px;
    border-radius: 0px 0px 5px 5px;
    transform: scaleY(0);
    transition: 0.5s;
    transform-origin: 50% 0 0;
    opacity: 0;
  }
  .menu.active {
    transform: scaleY(1);
    opacity: 1;
  }
}

.menu__link {
  position: relative;
  font-family: "Montserrat";
  font-size: 14px;
  padding: 43px 6px 40px 6px;
  display: block;
  margin-left: 18px;
  transition: 0.5s;
  border-radius: 2px 2px 0px 0px;
}

@media (max-width: 768px) {
  .menu__link {
    padding: 20px;
    margin-left: 0;
  }
}

.menu__link::after {
  content: '';
  height: 2px;
  background: #ED8E00;
  width: 0;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transition: 0.5s;
}

@media (max-width: 768px) {
  .menu__link::after {
    left: 0;
    width: 2px;
    height: 0;
    bottom: 50%;
  }
}

.menu__link:hover {
  color: #ED8E00;
}

@media (max-width: 768px) {
  .menu__link:hover {
    background: rgba(237, 142, 0, 0.1);
  }
}

.menu__link:hover::after {
  width: 100%;
  left: 0;
}

@media (max-width: 768px) {
  .menu__link:hover::after {
    bottom: 0;
    width: 2px;
    height: 100%;
  }
}

.banner {
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
}

.banner__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.banner__content {
  position: relative;
  background: rgba(7, 15, 25, 0.75);
  flex-grow: 1;
  padding: 15px 0;
  display: flex;
  align-items: center;
  font-family: "Montserrat";
  color: #fff;
}

.banner__title {
  font-size: 64px;
  font-weight: 700;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (max-width: 768px) {
  .banner__title {
    font-size: 45px;
  }
}

@media (max-width: 576px) {
  .banner__title {
    font-size: 30px;
    text-align: center;
  }
}

.banner__description {
  font-size: 16px;
  line-height: 24px;
  max-width: 570px;
  width: 100%;
  text-align: justify;
  color: #C8C8C8;
  font-weight: 400;
  margin-top: 22px;
  margin-bottom: 48px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (max-width: 576px) {
  .banner__description {
    text-align: center;
  }
}

.banner__links {
  display: flex;
}

@media (max-width: 576px) {
  .banner__links {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }
  .banner__links .btn {
    margin-bottom: 20px;
    margin-right: 0;
  }
}

.film {
  position: relative;
  border-radius: 6px;
  overflow: hidden;
}

.film__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.film__content {
  position: relative;
  background: linear-gradient(180deg, rgba(241, 129, 40, 0.35) 0%, rgba(0, 0, 0, 0.71) 51.04%, rgba(7, 15, 25, 0.8) 100%);
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  padding: 56px 15px 20px 15px;
  font-family: "Montserrat";
}

.film__link {
  line-height: 48px;
  font-size: 50px;
}

.film__time {
  font-size: 14px;
  line-height: 10px;
  padding: 7px 28px;
  border: 1px solid #fff;
  border-radius: 9px;
  background: rgba(7, 15, 25, 0.2);
  margin-top: 13px;
  margin-bottom: 32px;
}

.film__title {
  font-size: 22px;
  font-weight: 400;
  margin-bottom: 13px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
}

.film__desc {
  font-family: "Roboto";
  font-size: 14px;
  line-height: 21px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 7;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 20px;
}

.films {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

@media (max-width: 992px) {
  .films {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .films {
    grid-template-columns: 100%;
  }
}

.marvel {
  margin-top: 70px;
  background: linear-gradient(270deg, rgba(7, 15, 25, 0.56) 0%, rgba(49, 114, 9, 0.56) 100%);
  position: relative;
}

.marvel__bg {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  max-width: 829px;
  width: 100%;
  object-fit: cover;
}

.marvel__content {
  position: relative;
  background: linear-gradient(90deg, #DEF7FF 0%, #F4FCFF 45.64%, rgba(255, 255, 255, 0) 100.72%);
  padding-top: 43px;
  padding-bottom: 80px;
}

.marvel__item {
  display: flex;
  flex-direction: column;
}

@media (max-width: 768px) {
  .marvel__item {
    align-items: center !important;
  }
}

.marvel__ads {
  max-width: 450px;
  width: 100%;
  object-fit: cover;
}

@media (max-width: 576px) {
  .marvel__ads {
    max-width: 350px;
  }
}

.marvel__desc {
  font-size: 18px;
  font-weight: 400;
  font-family: "Roboto";
  line-height: 27px;
  margin-top: 50px;
  margin-bottom: 55px;
  text-align: justify;
  max-width: 558px;
  width: 100%;
}

@media (max-width: 768px) {
  .marvel__desc {
    text-align: center;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.marvel__link {
  width: max-content;
  margin: 0;
}

.coll {
  padding-top: 60px;
  padding-bottom: 60px;
}

.coll__cinemas {
  position: relative;
  border-radius: 6px;
  overflow: hidden;
}

.coll__img {
  position: absolute;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.coll__content {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 10px;
  background: linear-gradient(270deg, rgba(7, 15, 25, 0.56) 0%, rgba(49, 114, 9, 0.56) 100%);
}

.coll__article {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}

.coll__icon {
  line-height: 48px;
  font-size: 50px;
  display: inline-block;
}

.coll__title {
  font-size: 20px;
  font-weight: 700;
  margin-top: 15px;
  text-align: center;
}

.coll__item {
  display: grid;
  grid-template-rows: repeat(5, 1ft);
  gap: 15px;
  flex-direction: column;
}

.coll__value {
  width: 20px;
  height: 25px;
  border-radius: 2px;
  background: #040D1F;
}

.cinemas {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}

@media (max-width: 992px) {
  .cinemas {
    grid-template-columns: repeat(1, 0.85fr);
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .series__section1 {
    display: grid;
    grid-template-columns: 90%;
    justify-content: center;
  }
}

.series__ads {
  position: relative;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 30px;
}

.section1__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.section1__content {
  position: relative;
  background: linear-gradient(95.68deg, rgba(35, 35, 35, 0.8) 10.86%, rgba(11, 11, 11, 0.6) 110.86%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 104px 15px;
  color: #fff;
}

.section1__title {
  font-size: 40px;
  font-weight: 700;
  text-align: center;
}

@media (max-width: 576px) {
  .section1__title {
    font-size: 32px;
  }
}

@media (max-width: 400px) {
  .section1__title {
    font-size: 27px;
  }
}

.section1__desc {
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  line-height: 24px;
  margin: 30px 0;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  overflow: hidden;
  text-overflow: ellipsis;
}

.section1__link {
  margin: 0;
}

.part1 {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

@media (max-width: 768px) {
  .part1 {
    grid-template-columns: repeat(1, 0.9fr);
    justify-content: center;
  }
}

.part1__seria {
  position: relative;
  overflow: hidden;
  border-radius: 6px;
  display: flex;
}

.part1__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.part1__content {
  background: linear-gradient(95.68deg, rgba(35, 35, 35, 0.8) 10.86%, rgba(11, 11, 11, 0.6) 110.86%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 143px;
  padding-bottom: 162px;
  position: relative;
  color: #fff;
  flex-grow: 1;
}

@media (max-width: 768px) {
  .part1__content {
    padding-top: 45px;
    padding-bottom: 50px;
  }
}

.part1__link {
  line-height: 48px;
  font-size: 50px;
}

.part1__title {
  margin-top: 20px;
  font-weight: 700;
  font-size: 24px;
  text-align: center;
}

.part2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}

@media (max-width: 768px) {
  .part2 {
    grid-template-columns: 90%;
    justify-content: center;
  }
}

.part2__seria {
  position: relative;
  overflow: hidden;
  border-radius: 6px;
}

.part2__img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.part2__content {
  background: linear-gradient(95.68deg, rgba(35, 35, 35, 0.8) 10.86%, rgba(11, 11, 11, 0.6) 110.86%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 45px;
  padding-bottom: 50px;
  position: relative;
  color: #fff;
}

.part2__link {
  line-height: 48px;
  font-size: 50px;
}

.part2__title {
  margin-top: 20px;
  font-weight: 700;
  font-size: 24px;
}

.series__section2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}

@media (max-width: 768px) {
  .series__section2 {
    display: flex;
    flex-direction: column;
  }
}

.series {
  padding-bottom: 60px;
}

.footer {
  background: linear-gradient(90deg, #FF8C30 0%, #9F4700 100%);
  padding: 25px 0;
}

.footer__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

@media (max-width: 576px) {
  .footer__content {
    flex-direction: column;
    align-items: center;
  }
}

.footer__info {
  font-size: 18px;
  font-weight: 700;
  color: #fff;
}

@media (max-width: 576px) {
  .footer__info {
    width: 400px;
    margin-top: 17px;
    text-align: center;
  }
}

.footer__info-link {
  color: #fff;
  transition: 0.5s;
}

.footer__info-link:hover {
  color: #040D1F;
}

@media (max-width: 576px) {
  .footer__info-link {
    display: block;
    margin-top: 10px;
  }
}

.footer__link {
  display: block;
  font-size: 55px;
  line-height: 48px;
}

@media (max-width: 576px) {
  .footer__link {
    font-size: 48px;
    line-height: 43px;
  }
}
