$Fonts : ("Comfortaa", "Regular", 400),
("Comfortaa", "Bold", 700),
("Montserrat", "Regular", 400),
("Montserrat", "Bold", 700),
("fontawesome", "duotone", 900),
("fontawesome", "light", 300),
("Roboto", "Medium", 500),
("Roboto", "Regular", 400);

@each $family,
$name,
$weight in $Fonts {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}

$white : #fff;
$black : #000;
$ads: linear-gradient(90.09deg, #3800AF 0%, #BF7204 97.52%, #C27500 100%, #C27500 100%);
$nav:rgba(1, 3, 5, 0.8);
$nav_link: #ED8E00;
$banner: rgba(7, 15, 25, 0.75);
$grey :#C8C8C8;
$orange: #F18128;
$main: #040D1F;
$film: linear-gradient(180deg, rgba(241, 129, 40, 0.35) 0%, rgba(0, 0, 0, 0.71) 51.04%, rgba(7, 15, 25, 0.8) 100%);
$time: rgba(7, 15, 25, 0.2);
$coll: linear-gradient(270deg, rgba(7, 15, 25, 0.56) 0%, rgba(49, 114, 9, 0.56) 100%);
$ser: linear-gradient(95.68deg, rgba(35, 35, 35, 0.8) 10.86%, rgba(11, 11, 11, 0.6) 110.86%);
$footer: linear-gradient(90deg, #FF8C30 0%, #9F4700 100%);
$pr: linear-gradient(90deg, #DEF7FF 0%, #F4FCFF 45.64%, rgba(255, 255, 255, 0) 100.72%);
$bw: linear-gradient(270deg, rgba(7, 15, 25, 0.56) 0%, rgba(49, 114, 9, 0.56) 100%);