.footer {
    background: $footer;
    padding: 25px 0;

    &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        @include media ($xs) {
            flex-direction: column;
            align-items: center;
        }
    }

    &__info {
        font-size: 18px;
        font-weight: 700;
        color: $white;
        @include media ($xs) {
            width: 400px;
            margin-top: 17px;
            text-align: center;
        }
    }

    &__info-link {
        
        color: $white;
        transition: 0.5s;
        &:hover {
            color: $main;
        }
        @include media ($xs) {
            display: block;
           margin-top: 10px;
        }

    }

    &__link {
        display: block;
        font-size: 55px;
        line-height: 48px;

        @include media ($xs) {
            font-size: 48px;
            line-height: 43px;
          
        }
    }
}