.marvel {
    margin-top: 70px;
    background: $bw;
    position: relative;

    &__bg {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        max-width: 829px;
        width: 100%;
        object-fit: cover;

    }

    &__content {
        position: relative;
        background: $pr;
        padding-top: 43px;
        padding-bottom: 80px;

    }

    &__item {
        display: flex;
        flex-direction: column;

        @include media($sm) {
            align-items: center !important;
        }
    }

    &__ads {
        max-width: 450px;
        width: 100%;
        object-fit: cover;

        @include media ($xs) {
            max-width: 350px;
        }
    }

    &__desc {
        font-size: 18px;
        font-weight: 400;
        font-family: "Roboto";
        line-height: 27px;
        margin-top: 50px;
        margin-bottom: 55px;
        text-align: justify;
        max-width: 558px;
        width: 100%;

        @include media($sm) {
            text-align: center;
            @include limit(4);
        }
    }

    &__link {
        width: max-content;
        margin: 0;
    }

}